.community-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  padding: 20px;
}

.community-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.95);
  padding: 20px 30px;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;

  .header-left {
    h1 {
      margin: 0;
      font-size: 28px;
      color: #333;
    }

    .subtitle {
      margin: 5px 0 0;
      color: #666;
      font-size: 14px;
    }
  }

  .header-right {
    display: flex;
    align-items: center;
    gap: 15px;

    .user-info {
      display: flex;
      align-items: center;
      gap: 10px;

      .avatar {
        font-size: 32px;
      }

      .user-details {
        display: flex;
        flex-direction: column;

        .nickname {
          font-weight: 600;
          color: #333;
        }

        .level {
          font-size: 12px;
          color: #f5576c;
        }
      }
    }

    .btn-logout {
      padding: 8px 20px;
      background: #ff4757;
      color: white;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        background: #e84118;
        transform: translateY(-2px);
      }
    }
  }
}

.community-content {
  display: grid;
  grid-template-columns: 280px 1fr 300px;
  gap: 20px;
  max-width: 1600px;
  margin: 0 auto;

  @media (max-width: 1400px) {
    grid-template-columns: 250px 1fr;
    .right-sidebar {
      display: none;
    }
  }

  @media (max-width: 1024px) {
    grid-template-columns: 1fr;
    .left-sidebar {
      display: none;
    }
  }
}

// 공통 카드 스타일
.categories-card,
.chat-card,
.my-info-card,
.notice-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;

  h3 {
    margin: 0 0 15px;
    font-size: 18px;
    color: #333;
  }
}

.category-list {
  .category-item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 12px;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 8px;

    &:hover {
      background: #f8f9fa;
      transform: translateX(5px);
    }

    .cat-icon {
      font-size: 20px;
    }

    .cat-name {
      flex: 1;
      font-weight: 500;
    }

    .cat-count {
      font-size: 12px;
      color: #999;
    }
  }
}

.chat-card {
  .chat-messages {
    max-height: 300px;
    overflow-y: auto;
    margin-bottom: 10px;
    padding: 10px;
    background: #f8f9fa;
    border-radius: 10px;

    .chat-msg {
      display: flex;
      flex-direction: column;
      margin-bottom: 10px;
      padding: 8px;
      background: white;
      border-radius: 8px;

      .msg-user {
        font-weight: 600;
        font-size: 12px;
        color: #f5576c;
        margin-bottom: 4px;
      }

      .msg-text {
        color: #333;
        font-size: 14px;
      }

      .msg-time {
        font-size: 10px;
        color: #999;
        margin-top: 4px;
        align-self: flex-end;
      }
    }
  }

  .chat-input {
    display: flex;
    gap: 10px;

    input {
      flex: 1;
      padding: 10px;
      border: 1px solid #ddd;
      border-radius: 8px;
      font-size: 14px;

      &:focus {
        outline: none;
        border-color: #f5576c;
      }
    }

    button {
      padding: 10px 20px;
      background: #f5576c;
      color: white;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        background: #e04555;
      }
    }
  }
}

.main-content {
  h2 {
    margin: 0 0 20px;
    font-size: 24px;
    color: #fff;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  }
}

.posts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.post-card {
  background: white;
  border-radius: 15px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  }

  &.hot {
    border: 2px solid #ff6b6b;
  }

  .post-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;

    .post-category {
      font-size: 12px;
      color: #666;
      background: #f8f9fa;
      padding: 4px 12px;
      border-radius: 20px;
    }

    .hot-badge {
      font-size: 12px;
      background: linear-gradient(135deg, #ff6b6b, #ff4757);
      color: white;
      padding: 4px 12px;
      border-radius: 20px;
      font-weight: 600;
    }
  }

  .post-title {
    margin: 0 0 10px;
    font-size: 18px;
    color: #333;
    font-weight: 600;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .post-content {
    margin: 0 0 15px;
    color: #666;
    font-size: 14px;
    line-height: 1.6;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .post-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    border-top: 1px solid #f0f0f0;

    .post-author {
      display: flex;
      align-items: center;
      gap: 8px;

      .author-avatar {
        font-size: 20px;
      }

      .author-name {
        font-size: 14px;
        font-weight: 500;
        color: #333;
      }
    }

    .post-stats {
      display: flex;
      gap: 12px;

      .stat {
        font-size: 12px;
        color: #999;
      }
    }
  }

  .post-date {
    font-size: 11px;
    color: #999;
    margin-top: 8px;
  }
}

.posts-list {
  .post-item {
    background: white;
    border-radius: 10px;
    padding: 15px 20px;
    margin-bottom: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:hover {
      background: #f8f9fa;
      transform: translateX(5px);
    }

    .post-item-left {
      display: flex;
      gap: 12px;
      flex: 1;

      .post-avatar {
        font-size: 24px;
      }

      .post-info {
        flex: 1;

        .post-item-title {
          margin: 0 0 5px;
          font-size: 16px;
          color: #333;
          font-weight: 500;
        }

        .post-meta {
          display: flex;
          gap: 8px;
          font-size: 12px;
          color: #999;

          .meta-author {
            color: #666;
            font-weight: 500;
          }

          .meta-separator {
            color: #ddd;
          }
        }
      }
    }

    .post-item-stats {
      display: flex;
      gap: 15px;

      .stat-item {
        font-size: 14px;
        color: #999;
      }
    }
  }
}

.my-info-card {
  .profile-info {
    text-align: center;

    .profile-avatar-large {
      font-size: 64px;
      margin-bottom: 10px;
    }

    h4 {
      margin: 0 0 5px;
      font-size: 20px;
      color: #333;
    }

    .profile-level {
      margin: 0 0 15px;
      font-size: 14px;
      color: #f5576c;
      font-weight: 600;
    }

    .profile-stats {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 10px;
      margin-top: 15px;

      .stat-box {
        background: #f8f9fa;
        padding: 15px;
        border-radius: 10px;

        .stat-value {
          font-size: 24px;
          font-weight: 700;
          color: #f5576c;
        }

        .stat-label {
          font-size: 12px;
          color: #999;
          margin-top: 5px;
        }
      }
    }
  }
}

.notice-card {
  .notice-list {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      padding: 12px;
      border-bottom: 1px solid #f0f0f0;
      font-size: 14px;
      color: #666;
      cursor: pointer;
      transition: all 0.3s ease;

      &:last-child {
        border-bottom: none;
      }

      &:hover {
        background: #f8f9fa;
        color: #f5576c;
      }
    }
  }
}
